import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mySite from './modules/mysite'
import user from './modules/user'
import shop from './modules/shop'
//
Vue.use(Vuex);
export default new Vuex.Store({
  actions,
  getters,
  modules:{
    mySite,
    user,
    shop
  }
})



