<template>
  <footer class="footer_guide border-1px">
    <a href="javascript:;" class="guide_item" :class="{on: $route.path === '/mysite'}" @click="goto('/mysite')">
    <span class="item_icon">
      <i class="iconfont icon-waimai"></i>
    </span>
      <span></span>
    </a>
    <a href="javascript:;" class="guide_item" :class="{on: $route.path === '/search'}" @click="goto('/search')">
    <span class="item_icon">
      <i class="iconfont icon-search"></i>
    </span>
      <span></span>
    </a>
    <a href="javascript:;" class="guide_item" :class="{on: $route.path === '/order'}" @click="goto('/order')">
    <span class="item_icon">
      <i class="iconfont icon-dingdan"></i>
    </span>
      <span></span>
    </a>
    <a href="javascript:;" class="guide_item" :class="{on: $route.path === '/profile'}" @click="goto('/profile')">
    <span class="item_icon">
      <i class="iconfont icon-geren"></i>
    </span>
      <span></span>
    </a>
  </footer>
</template>

<script>
  export default {
    methods: {
      goto(path){
        this.$router.replace(path)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixins.styl"
  .footer_guide
    top-border-1px(#e4e4e4)
    position fixed
    z-index 100
    left 0
    right 0
    bottom 0
    background-color #fff
    width 100%
    height 50px
    display flex
    .guide_item
      display flex
      flex 1
      text-align center
      flex-direction column
      align-items center
      margin 5px
      color #999999
      &.on
        color #02a774
      span
        font-size 12px
        margin-top 2px
        margin-bottom 2px
        .iconfont
          font-size 22px
</style>



