<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay=true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay=false"></a>
        </div>
      </div>
      <div class="login_content">
        <form>
          <div :class="{on: loginWay}" >
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button :disabled="!isRightPhone || time>0"
                class="get_verification"
                :class="{right_phone_number: isRightPhone}" @click.prevent="sendCode">
                {{time>0 ? `(${time})`:''}}
              </button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="tel" maxlength="11" placeholder="//" v-model="name">
              </section>
              <section class="login_verification">
                <input :type="isToggle ? 'text':'password'" maxlength="8" placeholder="" v-model="pwd">
                <div class="switch_button off" @click="isToggle = !isToggle" :class="isToggle ? 'on':'off'">
                  <div class="switch_circle" :class="{right: isToggle}"></div>
                  <span class="switch_text" >{{isToggle ? '':''}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img ref="captcha" class="get_verification" src="http://localhost:5000/captcha" alt="captcha" @click="updateCaptcha">
              </section>
            </section>
          </div>
          <button class="login_submit" @click.prevent="login"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </a>
    </div>
  </section>
</template>

<script>
  import {Toast,MessageBox} from 'mint-ui'
  import {reqPwdLogin,reqSendCode,reqSmsLogin} from '../../api/index'
  export default {
    data(){
      return{
        //
        loginWay: false,// true: , false: 
        phone:'',//
        code:'',//
        name:'',//
        pwd: '',//
        captcha:'',//
        time: 0,//
        isToggle: false //
      }
    },
    computed: {
      isRightPhone () {
        return /^1\d{10}$/.test(this.phone)
      }
    },
    methods: {
      async sendCode () {
        // 
        this.time = 30
        // , 1s1
        const intervalId = setInterval(() => {
          this.time--
          if(this.time<=0) {
            // 
            clearInterval(intervalId)
          }
        }, 1000)
      //  
        const result = await reqSendCode(this.phone);
        if (result.code === 0){
          //
          Toast('')
        }else {
          //
          // 
          this.time = 0
          MessageBox.alert(result.msg)
        }
      },
      updateCaptcha(){
        this.$refs.captcha.src = 'http://localhost:5000/captcha?time'+ Date.now();
      },
    //  
      async login(){
        const {phone,name,pwd,code,captcha,loginWay} = this;
        let result;
      //
        if (loginWay){
          if (!this.isRightPhone){
            return MessageBox.alert("");
          } else if (!/^\d{6}$/.test(code)){
            return MessageBox.alert("");
          }
          //
          result = await reqSmsLogin(phone,code)
        }else {
          if (!name.trim()){
            return MessageBox.alert("");
          } else if (!pwd.trim()){
            return MessageBox.alert("");
          }else if (captcha.length !== 4){
            return MessageBox.alert("");
          }
          //
          result = await reqPwdLogin({name,pwd,captcha});
        }
        if (result.code === 0){
          const user = result.data;
          console.log(user);
          //state
          this.$store.dispatch('saveUser',user);
        //  profile
          this.$router.replace('/profile');
        }else {
          return MessageBox.alert(result.msg);
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .loginContainer
    width 100%
    height 100%
    background #fff
    .loginInner
      padding-top 60px
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 40px
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 40px
          text-align center
          >a
            color #333
            font-size 14px
            padding-bottom 4px
            &:first-child
              margin-right 40px
            &.on
              color #02a774
              font-weight 700
              border-bottom 2px solid #02a774
      .login_content
        >form
          >div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 10px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial
              &:focus
                border 1px solid #02a774
            .login_message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .get_verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent
                &.right_phone_number
                  color black
            .login_verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .switch_button
                font-size 12px
                border 1px solid #ddd
                border-radius 8px
                transition background-color .3s,border-color .3s
                padding 0 6px
                width 30px
                height 16px
                line-height 16px
                color #fff
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                &.off
                  background #fff
                  .switch_text
                    float right
                    color #ddd
                &.on
                  background #02a774
                >.switch_circle
                  position absolute
                  top -1px
                  left -1px
                  width 16px
                  height 16px
                  border 1px solid #ddd
                  border-radius 50%
                  background #fff
                  box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                  transition transform .3s
                  &.right
                    transform translateX(27px)
            .login_hint
              margin-top 12px
              color #999
              font-size 14px
              line-height 20px
              >a
                color #02a774
          .login_submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background #4cd96f
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0
        .about_us
          display block
          font-size 12px
          margin-top 20px
          text-align center
          color #999
      .go_back
        position absolute
        top 5px
        left 5px
        width 30px
        height 30px
        >.iconfont
          font-size 20px
          color #999
</style>



